// 引入vue
import Vue from 'vue'
import Vuex from 'vuex'

// 注册vuex
Vue.use(Vuex)

// 状态
const state = {
  userinfo: JSON.parse(localStorage.getItem('userinfo')),
  admininfo:JSON.parse(localStorage.getItem('admininfo')),
  isAdmin:JSON.parse(localStorage.getItem('isAdmin')),
  noPayData:JSON.parse(localStorage.getItem('noPayData'))
}

// moutations,用来改变操作state
const mutations = {
  // 保存用户数据
  SAVE_USERINFO (state, userinfo) {
    // 先把数据存入本地存储
    localStorage.setItem('userinfo', JSON.stringify(userinfo))
    localStorage.setItem('isAdmin', false)
    // 再更新数据
    state.userinfo = userinfo
    console.log(state.userinfo)
  },

  

  //获取管理员信息
  GET_ADMININFO(state,admininfo){
    console.log("请求成功，正在存入管理员数据，请稍后.....")
     // 先把数据存入本地存储
     localStorage.setItem('admininfo', JSON.stringify(admininfo))
     localStorage.setItem('isAdmin', true)
    state.admininfo=admininfo;
    console.log(state.admininfo)
  }

  //获取表格数据
}

const actions={
  GET_ADMININFO(admininfo){
    console.log("触发了该请求!")
    let _this = this
          const url = 'http://120.79.47.72/login'
          //  const url = 'http://192.168.43.139:8081/practiceSystem/LoginServlet';
          //  const url = 'http://192.168.43.102:8080/springmvcHandler/testJSON';
          // const url = 'http://192.168.43.77:8080/jeeTest/test';
        axios({
            method: 'post',
            url: url,
            data: this.ruleForm2
          }).then((res) => {
            // 存入本地存储
            localStorage.setItem('userinfo', JSON.stringify(this.ruleForm2.tel))
            let userinfo = JSON.parse(localStorage.getItem('userinfo'))
            // 把当前用户数据存入到state
            _this.$store.commit('GET_ADMININFO', userinfo)
            _this.$router.push('/home')

            alert(res.data.msg)
          })
    admininfo.commit('GET_ADMININFO');
  }
}

// 创建store仓库暴露出去
export default new Vuex.Store({
  state,
  mutations,
  actions
})



