import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/vuex/store'
// 引入组件
// 要告诉 vue 使用 vueRouter
Vue.use(VueRouter)
const routes = [
  {
    path: '/',
    component: () => import('@/components/login')
  },
  {
    path: '/admin_login',
    component: () => import('@/components/adminlogin')
  },
  {

    path: '/login',
    component: () => import('@/components/login')
  },
  {

    path: '/register',
    component: () => import('@/components/register')
  },
  {

    path: '/home',
    component: () => import('@/components/home'),
    children: [
      {
        path: 'userinfo',
        component: () => import('@/components/userinfo')
      },
      {

        path: 'employee',
        component: () => import('@/components/employee')
      },
      {
        path: 'addVip',
        component: () => import('@/components/addVip')
      },
      {
        path: 'statistics',
        component: () => import('@/components/Statistics')
      },
      {
        path: 'store',
        component: () => import('@/components/Store')
      },
      {
        path: 'setAdmin',
        component: () => import('@/components/Setting')
      },
      {
        path: 'member',
        component: () => import('@/components/Member')
      },
      {
        path: 'setting',
        component: () => import('@/components/Setting')
      },
      {
        path: 'pos',
        component: () => import('@/components/Pos')
      },
      {
        path: 'goods',
        component: () => import('@/components/Goods')
      },
      {
        path: 'user',
        component: () => import('@/components/user')
      },
      {
        path: 'usertop',
        component: () => import('@/components/topuser')
      }
    ]
  }

]

//全局路由守卫
// router.beforeEach((to,from,next)=>{
//   console.log("navigation-guards");
//   //to:Route: 即将要进入的目标 路由对象
//   // from: Route: 当前导航正要离开的路由
//   // next: Function: 一定要调用该方法来 resolve 这个钩子。执行效果依赖 next 方法的调用参数。
//   const nextRoute = ['home'];
// })
var router = new VueRouter({
  routes
})
export default router



